import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  NgZone
} from '@angular/core';
import {
  CombineService
} from '../../services/combine.service';
import {
  AppService
} from '../../services/app.service';
import {
  OnsNavigator
} from 'ngx-onsenui';
import {
  MatCheckbox
} from '@angular/material';
import {
  Measurement
} from 'src/app/models/measurement.model';

declare let navigator: any;
declare let window: any;
declare let cordova: any;

@Component({
  selector: 'ons-page[recordmeasurements]',
  templateUrl: './recordmeasurements.component.html',
  styleUrls: ['./recordmeasurements.component.scss']
})
export class RecordmeasurementsComponent implements OnInit {
  athleteName: string;
  athleteNumber: string;
  loopableDrills: Array < any > ;
  measurements = Array();
  drillTitle: string;
  flagged = false;
  athleteNumberSearched = false;
  errorMessages: string;
  showSuccess = false;
  showError = false;

  @ViewChild('athletenumber') athleteNumberField: ElementRef;

  constructor(
    public navi: OnsNavigator,
    public combineService: CombineService,
    public ngZone: NgZone,
    public appService: AppService
  ) {}

  ngOnInit() {
    this.retrievePendingMeasurements();
    if (this.combineService.drills.length > 1) {
      this.drillTitle = 'All Drills';
    } else {
      this.drillTitle = this.combineService.drills[0].name;
    }
    this.loopableDrills = [];
    this.combineService.drills.forEach(element => {
      if (element.numberOfRuns > 1) {
        for (let i = 0; i < element.numberOfRuns; i++) {
          const runElement = Object.assign({}, element);
          runElement.run = (i + 1);
          runElement.formName = 'drill_' + element.drillId + '_' + (i + 1);
          runElement.name = element.name + ' Run ' + runElement.run;
          if (element.precision) {
            runElement.validationMsg = 'Precision Required: ' + element.precision;
            runElement.validationPattern = new RegExp('^\\d*.\\d{' + element.precision + '}$');
          } else {
            runElement.validationMsg = '';
            runElement.validationPattern = new RegExp('(.*?)');
          }
          this.loopableDrills.push(runElement);
        }
      } else {
        element.run = 1;
        if (element.precision) {
          element.validationPattern = new RegExp('^\\d*.\\d{' + element.precision + '}$');
        } else {
          element.validationPattern = new RegExp('(.*?)');
        }
        this.loopableDrills.push(element);
      }
    });
    this.appService.loadingBar = false;
    this.appService.trackView('Record Measurements');
  }


  launchAthleteNameLookup() {
    if (!this.combineService.combine.searchAthleteBeforeShowingDrills) {
      this.athleteNameLookup();
    }
  }

  async athleteNameLookup(overrideNumber ? : string) {
    this.appService.loadingBar = true;
    if (overrideNumber) {
      this.athleteNumber = overrideNumber;
    }
    this.athleteName = '';
    if (this.athleteNumber !== '' && this.athleteNumber !== undefined && this.athleteNumber !== 'ERROR') {
      this.combineService.athleteNameLookup(this.athleteNumber).then((resp) => {
        if (resp.errorCode === 'notfound') {
          // no athlete found
          this.athleteName = 'No Athlete Found';
          this.athleteNumberSearched = true;
        } else {
          // ### TO DO: Pull previous run time from server
          this.ngZone.run(() => {
            this.athleteName = resp.name;
            this.athleteNumberSearched = true;
            this.loopableDrills.forEach(drillElement => {
              resp.measurements.forEach(measurementElement => {
                if (drillElement.drillId === measurementElement.drillId &&
                  drillElement.run === measurementElement.runNumber) {
                  drillElement.value = measurementElement.measurementValue;
                }
              });
            });
          });
        }
      });
    } else {
      this.athleteName = 'No Athlete Found';
      this.athleteNumberSearched = true;
    }
    this.appService.loadingBar = false;
  }

  toggleFlag(checkbox: MatCheckbox) {
    this.flagged = checkbox.checked;
  }

  checkMinMaxValues() {
    let outOfRangeValues = '';
    this.loopableDrills.forEach(element => {
      if (element.value < element.minValue || element.value > element.maxValue) {
        outOfRangeValues += element.name + ': ' + element.value + '\n';
      }
    });
    if (outOfRangeValues !== '') {
      navigator.notification.confirm(
        'The following measurements are higher or lower than typical for this drill. Please confirm the value before proceeding.\n\n' +
        outOfRangeValues,
        this.confirmSuspiciousValues,  // callback to invoke with index of button pressed
        'Suspicious Entry',            // title
        'Confirm,Cancel'          // buttonLabels
      );
    } else {
      this.sendMeasurementsToServer();
    }
  }

  confirmSuspiciousValues(btnIndex) {
    if (btnIndex === 1) {
      this.sendMeasurementsToServer();
    }
  }

  checkPrecisionValues(): boolean {
    let validated = true;
    this.loopableDrills.forEach(element => {
      if (!element.value.toString().match(element.validationPattern)) {
        validated = false;
        if (this.errorMessages) {
          this.errorMessages += element.name + ' must have a precision of ' + element.precision;
        } else {
          this.errorMessages = element.name + ' must have a precision of ' + element.precision;
        }
      }
    });
    return validated;
  }

  async sendMeasurementsToServer() {
    this.showSuccess = false;
    this.showError = false;
    this.appService.loadingBar = true;
    this.loopableDrills.forEach(element => {
      const thisMeasurement = new Measurement();
      thisMeasurement.athleteNumber = this.athleteNumber;
      thisMeasurement.drillId = element.drillId;
      thisMeasurement.measurementValue = element.value;
      thisMeasurement.runNumber = element.run;
      thisMeasurement.flagged = this.flagged;
      this.measurements.push(thisMeasurement);
    });
    const submitResponse = await this.combineService.submitMeasurements(this.measurements);
    if (submitResponse === 'success') {
      this.athleteName = '';
      this.athleteNumber = '';
      this.flagged = false;
      this.loopableDrills.forEach(drillElement => {
        drillElement.value = '';
      });
      this.showSuccess = true;
      this.measurements = [];
      localStorage.setItem('pendingMeasurements', JSON.stringify([]));
      this.appService.trackEvent('Measurements', 'Submit Measurement', this.athleteNumber);
    } else {
      localStorage.setItem('pendingMeasurements', JSON.stringify(this.measurements));
      this.showError = true;
      this.appService.trackEvent('Measurements', 'Could Not Submit', this.athleteNumber);
    }
  }

  async submitMeasurements() {
    this.errorMessages = null;
    if (this.checkPrecisionValues()) {
      if (this.athleteNumber) {
        this.checkMinMaxValues();
      } else {
        try {
          navigator.notification.alert(
            'Athlete Number is required.', // message
            null, // callback
            'Information Missing', // title
            'Continue' // buttonName
          );
        } catch (err) {
          console.log('Athlete Number is required.');
        }
      }
    } else {
      navigator.notification.alert(
        this.errorMessages, // message
        null, // callback
        'Precision Errors', // title
        'Continue' // buttonName
      );
    }
    this.appService.loadingBar = false;
  }

  scanBarcode() {
    cordova.plugins.barcodeScanner.scan(
      (result) => {
        this.athleteNumber = result.text;
        this.athleteNumberField.nativeElement.value = result.text;
        this.athleteNameLookup(this.athleteNumber);
      },
      (error) => {
        this.athleteNumberField.nativeElement.value = 'ERROR';
      }, {
        preferFrontCamera: false, // iOS and Android
        showFlipCameraButton: false, // iOS and Android
        showTorchButton: true, // iOS and Android
        torchOn: false, // Android, launch with the torch switched on (if available)
        saveHistory: true, // Android, save scan history (default false)
        prompt: 'Scan athlete measurement card', // Android
        resultDisplayDuration: 1000, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
        orientation: 'portrait', // Android only (portrait|landscape), default unset so it rotates with the device
        disableAnimations: false, // iOS
        disableSuccessBeep: false // iOS and Android
      }
    );
  }

  retrievePendingMeasurements() {
    const checkForPendingMeasurements = localStorage.getItem('pendingMeasurements');
    const pendingMeasurements = JSON.parse(checkForPendingMeasurements);
    if (pendingMeasurements) {
      if (pendingMeasurements.length) {
        for (const pendingMeasurement of pendingMeasurements) {
          this.measurements.push(pendingMeasurement);
        }
      }
    }
  }

}
