import { Component, OnInit } from '@angular/core';
import { CombineService } from '../../services/combine.service';
import { OnsNavigator } from 'ngx-onsenui';
import { PickdrillComponent } from '../pickdrill/pickdrill.component';
import * as moment from 'moment';
import Utils from 'src/app/services/utils';
import { AuthService } from '../../services/auth.service';
import { AppService } from '../../services/app.service';

declare let bounce: any;
declare let window: any;

@Component({
  selector: 'ons-page[combine-selection]',
  templateUrl: './combine-selection.component.html',
  styleUrls: ['./combine-selection.component.scss'],
  providers: [ AuthService ]
})
export class CombineSelectionComponent implements OnInit {
  public combineListing: Array<any>;

  constructor(
    public navi: OnsNavigator,
    public combineService: CombineService,
    public authService: AuthService,
    public appService: AppService
  ) {
    this.combineListing = [];
    this.appService.loadingBar = true;
  }

  logout() {
    this.authService.logout();
  }

  async ngOnInit() {
    await this.getCombineListing();
    this.appService.trackView('Combine Selection');
  }

  displayDate(targetDate) {
    return moment(targetDate).format('MMM Do, YYYY');
  }

  async getCombineListing() {
    this.combineListing = await this.combineService.scorerCombineListing();
    this.appService.loadingBar = false;
  }

  selectCombine(selectedCombine) {
    this.appService.loadingBar = true;
    this.combineService.combine = selectedCombine;
    this.navi.element.pushPage(PickdrillComponent, { animation: 'slide' });
  }

}
