import { Component, OnInit } from '@angular/core';
import { OnsNavigator } from 'ngx-onsenui';
import { CombineSelectionComponent } from '../combine-selection/combine-selection.component';
import { User } from '../../models/user.model';
import { AppService } from '../../services/app.service';
import { AuthService } from '../../services/auth.service';

declare let navigator: any;

@Component({
  selector: 'ons-page[login]',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  providers: [ AuthService ]
})
export class LoginComponent implements OnInit {
  username: string;
  password: string;
  errorMessage: string;
  version = 'Version 1.6.4';

  constructor(
    private authService: AuthService,
    private navi: OnsNavigator,
    public appService: AppService
  ) { }

  ngOnInit() {
    this.username = 'rob@shocasesports.com';
    this.password = 'hhhhhh1HH';
    this.appService.loadingBar = false;
  }

  async login() {
    this.appService.loadingBar = true;
    this.errorMessage = null;

    try {
      const user = new User(
        this.username,
        this.password
      );
      const authToken = await this.authService.login(user);
      if (!authToken) {
        throw new Error('Unknown error');
      }

      localStorage.setItem('Authorization', authToken);

      this.navi.element.pushPage(CombineSelectionComponent, { animation: 'lift' });
    } catch (err) {
      this.appService.loadingBar = false;
      try {
        navigator.notification.alert(
          err.message,           // message
          null,                  // callback
          'Login Unsuccessful',  // title
          'Close'                // buttonName
        );
      } catch (alerterr) {
        console.log('TCL: LoginComponent -> }catch -> err', err);
        console.log('TCL: LoginComponent -> }catch -> alerterr', alerterr);
      }
    }

  }


}
