import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';

declare let window: any;

@Injectable({
  providedIn: 'root'
})
export class AppService {
  public loadingBar = true;

  constructor() {}

  public initAnalytics(analyticsId: string) {
    if (environment.production && window.ga) {
      window.ga.startTrackerWithId(analyticsId, 30);
    }
  }

  public trackView(viewName: string) {
    if (environment.production && window.ga) {
      window.ga.trackView(viewName);
    }
  }

  public trackEvent(category: string, action: string, label: string, value?: number) {
    if (environment.production && window.ga) {
      if (value) {
        window.ga.trackEvent(category, action, label, value);
      } else {
        window.ga.trackEvent(category, action, label);
      }
    }
  }
}
