import { Component, OnInit, ViewChild, ElementRef, NgZone } from '@angular/core';
import { CombineService } from '../../services/combine.service';
import { OnsNavigator } from 'ngx-onsenui';
import { MatCheckbox } from '@angular/material';
import { Measurement } from 'src/app/models/measurement.model';
import { AppService } from '../../services/app.service';

declare let navigator: any;
declare let window: any;
declare let cordova: any;

@Component({
  selector: 'ons-page[remove-athlete]',
  templateUrl: './remove-athlete.component.html',
  styleUrls: ['./remove-athlete.component.scss']
})
export class RemoveAthleteComponent implements OnInit {
  athleteName: string;
  athleteNumber: string;
  dataSourceUniqueID: string;
  removeChecker: string;

  @ViewChild('athletenumber') athleteNumberField: ElementRef;

  constructor(
    public navi: OnsNavigator,
    public combineService: CombineService,
    public ngZone: NgZone,
    public appService: AppService
  ) { }

  ngOnInit() {
    this.appService.loadingBar = false;
    this.appService.trackView('Remove Athlete');
  }

  async athleteNameLookup(overrideNumber?: string) {
    this.appService.loadingBar = true;
    if (overrideNumber) {
      this.athleteNumber = overrideNumber;
    }
    this.athleteName = '';
    if (this.athleteNumber !== '' && this.athleteNumber !== undefined && this.athleteNumber !== 'ERROR') {
      this.combineService.athleteNameLookup(this.athleteNumber).then((resp) => {
        if (resp.code === 404) {
          // # TO DO: no athlete found, disable button
        } else {
          this.ngZone.run(() => {
            this.athleteName = resp.name;
            // # TO DO: enable button
          });
        }
      });
    }
    this.appService.loadingBar = false;
  }

  async removeAthlete() {
    if (this.removeChecker.toLowerCase() === 'remove' && this.dataSourceUniqueID && this.athleteNumber) {
      this.appService.loadingBar = true;
      const callResp = await this.combineService.removeAthlete(this.dataSourceUniqueID, this.athleteNumber);
      if (callResp === 'success') {
        this.athleteNumberField.nativeElement.focus();
        try {
          navigator.notification.alert(
            this.athleteName + ' has been removed from the leaderboard.',  // message
            null,         // callback
            'Athlete Removed',            // title
            'Continue'                  // buttonName
          );
        } catch (err) {
          console.log(this.athleteName + ' has been removed from the leaderboard.');
        }
        this.athleteName = '';
        this.athleteNumber = '';
        this.removeChecker = '';
        this.appService.trackEvent('Admin', 'Remove Athlete', this.athleteNumber);
      } else {
        try {
          navigator.notification.alert(
            'An error was encountered. Check your inputs and try again.',  // message
            null,         // callback
            'Error',            // title
            'Continue'                  // buttonName
          );
        } catch (err) {
          console.log('An error was encountered. Check your inputs and try again.');
        }
      }
    } else {
      try {
        navigator.notification.alert(
          'You must include an athlete ID, the leaderboard data source ID and type REMOVE into the validation box.',  // message
          null,         // callback
          'Error',            // title
          'Continue'                  // buttonName
        );
      } catch (err) {
        console.log('An error was encountered. Check your inputs and try again.');
      }
    }
    this.appService.loadingBar = false;
  }

  scanBarcode() {
    cordova.plugins.barcodeScanner.scan(
      (result) => {
        this.athleteNumber = result.text;
        this.athleteNumberField.nativeElement.value = result.text;
        this.athleteNameLookup(this.athleteNumber);
      },
      (error) => {
        this.athleteNumberField.nativeElement.value = 'ERROR';
      },
      {
        preferFrontCamera : false, // iOS and Android
        showFlipCameraButton : false, // iOS and Android
        showTorchButton : true, // iOS and Android
        torchOn: false, // Android, launch with the torch switched on (if available)
        saveHistory: true, // Android, save scan history (default false)
        prompt : 'Scan athlete measurement card', // Android
        resultDisplayDuration: 1000, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
        orientation : 'portrait', // Android only (portrait|landscape), default unset so it rotates with the device
        disableAnimations : false, // iOS
        disableSuccessBeep: false // iOS and Android
      }
    );
  }

}
