import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { BaseService } from './base.service';
import { PortalService } from './portal.service';
import { Combine } from '../models/combine.model';
import { Drill } from '../models/drill.model';

@Injectable()
export class CombineService extends BaseService {
  public authToken: string;
  public combine: Combine;
  public drills: Array<Drill>;

  constructor(protected http: Http, protected portalService: PortalService) {
    super(http, portalService);
  }

  async submitMeasurements(measurementValues: Array<any>) {
    return this.post('drill/measurements',
      { combineId: this.combine.combineId, measurements: measurementValues });
  }

  async athleteNameLookup(athleteNumber: string) {
    return this.get('athleteNumber/' + athleteNumber + '/' + this.combine.combineId + '/name');
  }

  async scorerCombineListing() {
    return this.get('combine/mine');
  }

  async combineDrillListing() {
    return this.get('drill/combine/' + this.combine.combineId);
  }

  async removeAthlete(dataSourceUniqueID: string, athleteNumber: string) {
    return this.post('leaderboard/removeAthlete', { dataSourceUniqueID: dataSourceUniqueID, athleteNumber: athleteNumber });
  }

 }
