import { Http, Headers, Response } from '@angular/http';
import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { PortalService } from './portal.service';

export class BaseService {

  private baseUrl = environment.baseUrl;
  protected _subject$: Subject<Array<any>>;

  constructor(protected http: Http, protected portalService: PortalService) {
    this._subject$ = <Subject<Array<any>>>new Subject();
  }

  protected getBaseUrl(): string {
    return this.baseUrl;
  }

  protected get(route: string, options?: BaseServiceOptions): Promise<any> {
    if (!options) { options = {}; }
    return new Promise((resolve, reject) => {

      this.http.get(this.constructFullRoute(route), {headers: this.buildHeaders(options.excludeAuthorization, options.customToken)})
      .pipe(map(response => response.json())).subscribe(
        data => {
          resolve(data.data);
        },
        err => {
          reject(this.extractError(err));
        });
    });
  }

  protected delete(route: string, options?: BaseServiceOptions): Promise<any> {
    if (!options) { options = {}; }
    return new Promise((resolve, reject) => {

      this.http.delete(this.constructFullRoute(route), {headers: this.buildHeaders(options.excludeAuthorization, options.customToken)})
      .pipe(map(response => response.json())).subscribe(
        data => {
          resolve(data.data);
        },
        err => {
          reject(this.extractError(err));
        });
    });
  }

  protected post(route: string, payload: any, options?: BaseServiceOptions): Promise<any> {
    if (!options) { options = {}; }
    return new Promise((resolve, reject) => {

      this.http.post(this.constructFullRoute(route), payload,
      {headers: this.buildHeaders(options.excludeAuthorization, options.customToken)})
      .pipe(map(response => response.json())).subscribe(
        data => {
          resolve(data.data);
        },
        err => {
          reject(this.extractError(err));
        });
    });
  }

  protected patch(route: string, payload: any, options?: BaseServiceOptions): Promise<any> {
    if (!options) { options = {}; }
    return new Promise((resolve, reject) => {

      this.http.patch(this.constructFullRoute(route), payload,
      {headers: this.buildHeaders(options.excludeAuthorization, options.customToken)})
      .pipe(map(response => response.json())).subscribe(
        data => {
          resolve(data.data);
        },
        err => {
          reject(this.extractError(err));
        });
    });
  }

  private constructFullRoute(route: string): string {
    return this.baseUrl + this.portalService.portalId + '/' + route;
  }

  private buildHeaders(excludeAuthorization?: boolean, customToken?: string): Headers {
    const headers = new Headers();
    if (excludeAuthorization) {
      return headers;
    }

    let token = customToken;
    if (!token) {
      token = localStorage.getItem('Authorization');
    }
    if (!token) {
      return headers;
    }

    headers.set('Authorization', token);
    return headers;
  }

  private extractError(error: Response | any) {
    let message: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      message = body.message || JSON.stringify(body);
    } else {
      message = error.message ? error.message : error.toString();
    }

    return {
      message: message,
      code: error.status
    };
  }
}

export class BaseServiceOptions {
  excludeAuthorization?: boolean;
  customToken?: string;
}
