import { Component, OnInit } from '@angular/core';
import { CombineService } from '../../services/combine.service';
import { OnsNavigator } from 'ngx-onsenui';
import { AppService } from '../../services/app.service';
import { RemoveAthleteComponent } from '../remove-athlete/remove-athlete.component';

declare let navigator: any;

@Component({
  selector: 'ons-page[admin-functions]',
  templateUrl: './admin-functions.component.html',
  styleUrls: ['./admin-functions.component.scss']
})
export class AdminFunctionsComponent implements OnInit {

  constructor(
    public navi: OnsNavigator,
    public combineService: CombineService,
    public appService: AppService
  ) { }

  ngOnInit() {
    this.appService.loadingBar = false;
    this.appService.trackView('Admin Functions');
  }

  goToRemoveAthlete() {
    this.navi.element.pushPage(RemoveAthleteComponent, { animation: 'slide' });
  }

  comingSoon() {
    navigator.notification.alert(
      'This admin feature is planned for a future release.', // message
      null, // callback
      'Coming Soon', // title
      'Continue' // buttonName
    );
  }

}
