import { Component, OnInit } from '@angular/core';
import { onsPlatform } from 'ngx-onsenui';
import { LoginComponent } from './login/login.component';
import { CombineService } from '../services/combine.service';
import { PortalService } from '../services/portal.service';
import { AppService } from '../services/app.service';

declare let shocaseDebug: any;
declare let window: any;

@Component({
  selector: 'app-root',
  templateUrl: './appnavigator.html',
  styleUrls: [],
  providers: [ CombineService ]
})
export class NavigatorComponent implements OnInit {
  constructor(
    public combineService: CombineService,
    public portalService: PortalService,
    public appService: AppService
  ) {
    portalService.portalId = 2; // hardcoded to Rivals
}

  initialPage = LoginComponent;

  ngOnInit() {

    // SET ONSEN CONFIGS
    if (onsPlatform.isIPhoneX()) {
      const html = document.documentElement;
      const body = document.body;
      html.setAttribute('onsflag-iphonex-portrait', '');
      html.setAttribute('onsflag-iphonex-landscape', '');
      body.className = 'iphone-x';
    }

    // DEBUGGING
    shocaseDebug = this.combineService;

    // INIT
    // Initialize Google Analytics
    this.appService.initAnalytics('UA-130094608-2');
    // Keep screen on at all times during app usage.
    // window.plugins.insomnia.keepAwake();
  }
}
