import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { CombineService } from '../../services/combine.service';
import { OnsNavigator } from 'ngx-onsenui';
import { RecordmeasurementsComponent } from '../recordmeasurements/recordmeasurements.component';
import * as moment from 'moment';
import { AdminFunctionsComponent } from '../admin-functions/admin-functions.component';
import { AppService } from '../../services/app.service';

declare let bounce: any;
declare let window: any;

@Component({
  selector: 'ons-page[pickdrill]',
  templateUrl: './pickdrill.component.html',
  styleUrls: ['./pickdrill.component.scss']
})
export class PickdrillComponent implements OnInit {
  public drillListing: Array<any>;

  constructor(
    public navi: OnsNavigator,
    public combineService: CombineService,
    public appService: AppService
  ) {
    this.drillListing = [];
  }

  async ngOnInit() {
    await this.getDrillListing();
    this.appService.trackView('Pick Combine/Drill');
  }

  async getDrillListing() {
    this.drillListing = await this.combineService.combineDrillListing();
    this.appService.loadingBar = false;
  }

  displayDate(targetDate) {
    return moment(targetDate).format('MMM Do, YYYY');
  }

  goToAdminFunctions() {
    this.navi.element.pushPage(AdminFunctionsComponent, { animation: 'lift' });
  }

  selectDrill(drill) {
    this.appService.loadingBar = true;
    if (drill === 'all') {
      this.combineService.drills = this.drillListing;
    } else {
      this.combineService.drills = [drill];
    }
    this.navi.element.pushPage(RecordmeasurementsComponent, { animation: 'slide' });
  }

}
