import { Injectable, EventEmitter, Output } from '@angular/core';
import { Http } from '@angular/http';

import { BaseService } from './base.service';
import { User } from '../models/user.model';
import { PortalService } from './portal.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService extends BaseService {
  @Output() loggedIn: EventEmitter<User>;

  constructor(protected http: Http, protected portalService: PortalService) {
    super(http, portalService);
    this.loggedIn = new EventEmitter();
  }

  async login(user: User) {
    try {
      const token = await this.post('login', user, {excludeAuthorization: true});
      localStorage.setItem('Authorization', token);
      this.loggedIn.emit(user);
      return token;
    } catch (err) {
      throw err;
    }
  }

  async forgotPassword(email: string) {
    return this.post('forgotPassword', {email: email}, {excludeAuthorization: true});
  }

  async resetPassword(password: string, resetToken: string) {
    return this.post('resetPassword', {password: password}, {customToken: resetToken});
  }

  async register(user: User) {
    return this.post('register', user, {excludeAuthorization: true});
  }

  async updateProfile(user: User) {
    return this.post('user/profile', user);
  }

  async userUpdated(user: User) {
    this.loggedIn.emit(user);
  }

  async checkToken(token?: string) {
    return this.get('checkToken', { customToken: token });
  }

  async getProfile(): Promise<User> {
    return this.get('user/profile');
  }

  logout() {
    localStorage.removeItem('Authorization');
    this.loggedIn.emit(null);
  }
}
