import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class PortalService {
  // TODO figure out how we want to handle this
  private _portalId: number;

  get portalId() {
    return this._portalId;
  }

  set portalId(portalId: number) {
    this._portalId = portalId;
  }
}
